import { http } from "@/common/http";
import { ResponseType } from "@/common/http/overrideHttpType";

let baseUrl = "/nestApi/comment/";
// 
function addApi(data) {
	return http.request({
		url: baseUrl + "create",
		method: "POST",
		data,
	});
}

// 
function deleteApi(data) {
	return http.request({
		url: baseUrl + "delete",
		method: "POST",
		data,
	});
}

// 
function upadteApi(data) {
	return http.request({
		url: baseUrl + "update",
		method: "POST",
		// NOTE: idcomment_id
		data: { ...data, comment_id: data.id },
	});
}

// 
function queryApi<T>(data: any) {
	// , . .
	const _data = { ...data }
	Object.keys(data).forEach((key) => {
		if (_data[key] === "") {
			_data[key] = undefined;
		}
	});

	return http.request<{},ResponseType<{count:number,content:T[]}>>({
		url: baseUrl + "query",
		method: "POST",
		data: _data
	});
}

const devApi = {
	addApi,
	deleteApi,
	upadteApi,
	queryApi,
};

export default devApi;
