import { http } from "@/common/http";

// post
function fetchPageList() {
  return http.request({
    url: "/api/pageDev/getPageList",
    method: "GET",
  });
}

// 
function adcompanyPageList(params) {
  return http.request({
    url: "/api/pageDev/addPage",
    method: "POST",
    data: params,
  });
}

const devApi = {
  fetchPageList,
  adcompanyPageList,
};

export default devApi;
