import { ModuleEvent, WatchAPI } from "natur";

const watch = {
	// 
	userInfoStore: (event: ModuleEvent, api: WatchAPI) => {
		const {
			actionName,
			newModule: { state },
		} = event;
		if (actionName === "login" && event.type === "update") {
			api.localDispatch("createRoutesAction", state);
		}
	},
};

export default watch;
