import { http } from "@/common/http";
import { ResponseType } from "@/common/http/overrideHttpType";

let baseUrl = "/nestApi/user/";
// 
function addApi(data) {
	return http.request({
		url: baseUrl + "create",
		method: "POST",
		data,
	});
}

// 
function deleteApi(data) {
	return http.request({
		url: baseUrl + "delete",
		method: "POST",
		data,
	});
}

// 
function upadteApi(data) {
	return http.request({
		url: baseUrl + "update",
		method: "POST",
		data,
	});
}

// 
function queryApi<T>(data: any) {
	return http.request<{},ResponseType<{count:number,content:T[]}>>({
		url: baseUrl + "query",
		method: "POST",
		data,
	});
}

const devApi = {
	addApi,
	deleteApi,
	upadteApi,
	queryApi,
};

export default devApi;
